.skillBlock {
    /*width: 377px;*/
    width: 50%;
    height: 382px;
    /*background-color: #4c8ba2;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    padding-top: 71px;
    .icon {
        width: 80%;
        height: 85%;
        /*background-color: #e39090;*/
        /*overflow: hidden;*/
        box-shadow: 2px 2px 7px rgba(0,0,0,.2);
        border: 15px solid #fff;
    }
    @media (max-width: 750px) {
        height: 322px;
    }
    @media (max-width: 570px) {
        width: 70%;
    }
}

